import "./button.css";
import "./color_root.css";

export const createButton = ({ primary = false, size = "medium", color = "p", backgroundColor, label, onClick }) => {
    const btn = document.createElement("btn");
    btn.type = "button";
    btn.innerText = label;
    btn.addEventListener("click", onClick);

    const mode = primary ? "bhw" : "";
    btn.className = ["btn", `btn-${color}`, `btn-${size}`, mode].join(" ");

    btn.style.backgroundColor = backgroundColor;

    return btn;
};
